
.NavigationCompForArrow {
  z-index: 1;
  position: absolute;

  .wrap {
    position: relative;
    width: 80vmin;
    height: 80vmin;
    margin: 0 auto;
    background: inherit;
    transform: scale(0.8) translatez(0px);
    opacity: 1;
    transition: transform .5s, opacity .5s;

    /*&:hover {
      transform: scale(.8) translateZ(0px);
      opacity: 1;

      a {
        transform: scale(1) translatez(0px);
      }
    }*/
  }

  a {
    position: absolute;
    left: 0;
    top: 0;
    width: 47.5%;
    height: 47.5%;
    overflow: hidden;
    //transform: scale(.5) translateZ(0px);
    transform: scale(1) translateZ(0px);
    background: #585247;

    div {
      height: 100%;
      background-size: cover;
      opacity: .5;
      transition: opacity .5s;
      border-radius: inherit;
    }

    &:nth-child(1) {
      border-radius: 40vmin 0 0 0;
      transform-origin: 110% 110%;
      transition: transform .4s .15s;

      div {
        background-image: url('https://farm3.staticflickr.com/2827/10384422264_d9c7299146.jpg');
      }
    }

    &:nth-child(2) {
      border-radius: 0 40vmin 0 0;
      left: 52.5%;
      transform-origin: -10% 110%;
      transition: transform .4s .2s;

      div {
        background-image: url('https://farm7.staticflickr.com/6083/6055581292_d94c2d90e3.jpg');
      }
    }

    &:nth-child(3) {
      border-radius: 0 0 0 40vmin;
      top: 52.5%;
      transform-origin: 110% -10%;
      transition: transform .4s .25s;

      div {
        background-image: url('https://farm7.staticflickr.com/6092/6227418584_d5883b0948.jpg');
      }
    }

    /*&:nth-child(4) {
      border-radius: 0 0 40vmin 0;
      top: 52.5%;
      left: 52.5%;
      transform-origin: -10% -10%;
      transition: transform .4s .3s;

      div {
        background-image: url('https://farm8.staticflickr.com/7187/6895047173_d4b1a0d798.jpg');
      }
    }*/

    &:nth-child(4) {
      width: 55%;
      height: 55%;
      left: 22.5%;
      top: 22.5%;
      border-radius: 50vmin;
      box-shadow: 0 0 0 5vmin #E3DFD2;
      transform: scale(1);

      div {
        background-image: url('https://farm4.staticflickr.com/3766/12953056854_b8cdf14f21.jpg');
      }
    }

    &:hover {
      div {
        opacity: 1;
        transform: translatez(0px);
      }
    }
  }

  span {
    position: relative;
    display: block;
    margin: 0 auto;
    top: 45vmin;
    width: 10vmin;
    height: 10vmin;
    border-radius: 100%;
    background: #585247;
    transform: translateZ(0px);

    span {
      position: absolute;
      width: 60%;
      height: 3px;
      background: #ACA696;
      left: 20%;
      top: 50%;
      border-radius: 0;

      &:after {
        content: '';
        position: absolute;
        left: 0;
        top: -1.5vmin;
        width: 100%;
        height: 100%;
        background: inherit;
        top: 1.5vmin;
      }

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: -1.5vmin;
        width: 100%;
        height: 100%;
        background: inherit;
      }
    }

    &:hover {
      + {
        .wrap {
          transform: scale(.8) translateZ(0px);
          opacity: 1;

          a {
            transform: scale(1) translatez(0px);
          }
        }
      }
    }
  }
}